import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    updatedAnswerByUserData:[],
  },
  mutations: {
    updatedAnswerByUser(state,data){
      state.updatedAnswerByUserData=data;
    },
  },
  actions: {
    answerByUser(context,data){
      context.commit('updatedAnswerByUser',data);
    },
  },
  getters:{
    answerByUserGet:state=>state.updatedAnswerByUserData,
  }
});
