export const restrictInput= function (el,key,arg) {
  el.addEventListener('input', function(event) {
    // old code start keypress
      // if(arg.data.attrs.maxLength==event.target.value.length){
      //     event.preventDefault();
      // }
    // old code end

    //new code start  // input
      let num=new RegExp("[^0-9]+", "g");
      if(num.test(event.target.value)){
        event.target.value="";
        event.preventDefault();
      }
    //new code start
  });
}