/**
 * @module root\plugin\components\filePicker
 */
import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import Deferred from "@/extras/Deferred";

@Component
export default class FilePicker extends Vue {
  @Prop({
    default: "",
    type: String
  })
  prependIcon!: any;
  @Prop() value!: FilePickerModel;

  @Prop() errorMessages!: any;
  @Prop({
    type: String,
    required: true
  })
  label!: string;
  fileName = "";
  uploading = false;

  handleFileSelect(event: any) {
    if (event.target.files.length) {
      let file = event.target.files[0];
      let deferred = new Deferred();
      this.fileName = file.name;
      this.uploadFile(file, deferred);
    } else {
      this.removeFile();
    }
  }

  cancelUpload() {
    this.uploading = false;
    this.removeFile();
  }

  uploadFile(file: File, deferred: Deferred<any>) {
    this.uploading = true;
    new Promise((resolve, reject) => {
      setTimeout(() => {
        resolve({
          fileId: "ddgyuw",
          url: file
        });
      }, 500);
    })
      .then(data => {
        let reader = new FileReader();
        let base64: any = "";
        reader.addEventListener(
          "load",
          () => {
            base64 = reader.result;
            this.$emit("input", {
              file,
              promise: deferred.promise,
              base64: base64
            } as FilePickerModel);
          },
          false
        );
        if (file) {
          reader.readAsDataURL(file);
        }
        this.uploading = false;
      })
      .catch(err => {
        deferred.reject(err);
        this.uploading = false;
        this.removeFile();
      });
  }

  removeFile() {
    this.$emit("input", undefined);
    this.fileName = "";
    (this.$refs.fileInput as HTMLInputElement).value = "";
  }

  openFileInput() {
    (this.$refs.fileInput as HTMLInputElement).click();
  }
}

export interface FilePickerModel {
  file: File;
  promise: Promise<any>;
}
