import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);
const router =  new Router({
  mode: 'history',
  base: '/',
  routes: [
          {
            path: '/',
            name: 'home',
            component: ()=>import("./components/home/home.vue"),
            },
            {
              path: "*",
              redirect: "/",
            },
   ]
   
});
export default router;


