import { PluginObject } from "vue";
import commonMessages from "@/validations/messages";
import filePicker from "@/plugins/filePicker/filePicker";
import { restrictInput } from '@/services/directives/restrictInput';
let commonPlugin: PluginObject<{}> = {
  install(Vue) {
    Vue.prototype.$errorMessages = commonMessages;
    Vue.component("filePicker", filePicker);
    Vue.directive("restrict-input",restrictInput);
  }
};

export default commonPlugin;
