import Vue from "vue";
import VueI18n from "vue-i18n";
import hindi from "./hi";
import english from "./en";

Vue.use(VueI18n);

const i18n = new VueI18n({
  locale: "EN",
 // fallbackLocale: 'hindi',
//  messages: { english }
});



const i18nService = {
  setLanguage
};

//const loadedLanguages = ['hindi'];


/**
 * Changes language of app dynamically fetching language file from translations folder
 *
 * @author Pranav Jindal
 * @param {string} lang language key (must be same as file name in translations folder)
 * @returns
 */

function setLanguage(lang) {
  if (i18n.locale !== lang) {
      if(lang == "EN") {
        i18n.setLocaleMessage(lang,english);
      }else {
        i18n.setLocaleMessage(lang,hindi);
      }
    
    return Promise.resolve(setLanguageInternal(lang));
  }
  return Promise.resolve(lang);
}

/**
 * Changes document's language and i18n instance language
 *
 * @author Pranav Jindal
 * @param {string} lang
 * @returns
 */
function setLanguageInternal(lang: string) {
  i18n.locale = lang;
  document.documentElement.setAttribute("lang", lang);
  return lang;
}
export {
  i18n
};
export default i18nService;
