<template>
  <div>
    <v-layout row align-center class="relative">
      <v-text-field
        @click.native="openFileInput"
        :label="label"
        :prepend-icon="prependIcon"
        v-model="fileName"
        :readonly="true"
        :loading="uploading"
        :error-messages="errorMessages"
      ></v-text-field>

      <v-btn
        flat
        small
        class="browse-button"
        v-show="!fileName"
        @click="openFileInput"
        color="primary"
        :loading="false"
      >Browse</v-btn>

      <v-btn
        v-show="uploading"
        @click="cancelUpload"
        color="warning"
        flat
        small
        class="browse-button"
      >Cancel Upload</v-btn>

      <v-btn
        v-show="!uploading && fileName"
        @click="removeFile"
        color="error"
        flat
        small
        class="browse-button"
      >Remove File</v-btn>
    </v-layout>
    <input ref="fileInput" type="file" @change="handleFileSelect" :hidden="true">
  </div>
</template>

<style scoped>
.text-field,
.text-field >>> input {
  cursor: pointer;
}
.browse-button {
  right: 0;
  margin: 0;
  background: rgb(255, 255, 255);
}
.relative {
  position: relative;
}

</style>

<script>
import FilePicker from "./filePicker.ts";
export default FilePicker;
</script>
