
<template>
  <div>
    <template v-for="dialogOptions in dialogs">
      <v-dialog 
        @update:returnValue="dialogOptions.dialogRef.close()"
        :ref="dialogOptions.id"
        :key="dialogOptions.id"
        :full-width="dialogOptions.fullWidth"
        :fullscreen="dialogOptions.fullscreen"
        :lazy="true" 
        :persistent="dialogOptions.persistent"
        :max-width="dialogOptions.maxWidth"
        :width="dialogOptions.width"
        :scrollable="true" 
        transition="slide-y-reverse-transition"
        v-model="dialogOptions.active"
        >
        <component 
          :ref="dialogOptions.compId"
          :is="dialogOptions.component" 
          v-bind="dialogOptions.props">
          <div 
            slot="header" 
            slot-scope="headerProps">
            <v-toolbar 
              dark 
              class="elevation-1"
              color="primary">
              <v-toolbar-title class="white--text">{{ headerProps.title }}</v-toolbar-title>
              <v-spacer></v-spacer>
              <v-tooltip 
                v-show="headerProps.fullscreenButton"
                :close-delay="0" 
                bottom>
                <v-btn 
                  slot="activator"
                  @click="dialogOptions.fullscreen = !dialogOptions.fullscreen" 
                  icon>
                  <v-icon v-show="dialogOptions.fullscreen">fullscreen_exit</v-icon>
                  <v-icon v-show="!dialogOptions.fullscreen">fullscreen</v-icon>
                </v-btn>
                <span>{{ dialogOptions.fullscreen?'Fullscreen off':'Fullscreen on' }}</span>
              </v-tooltip>
              <v-tooltip 
                :close-delay="0" 
                bottom>
                <v-btn 
                  slot="activator"
                  @click="dialogOptions.dialogRef.close()" 
                  icon>
                  <v-icon>close</v-icon>
                </v-btn>
                <span>Close</span>
              </v-tooltip>              
            </v-toolbar>
          </div>
        </component>
      </v-dialog>
    </template>
  </div>
</template>



<script>
import sDialogComponent from "./sDialog.ts";
export default sDialogComponent;
</script>
